.dashboard{display:flex;height:50vw}.dashboard #nav-dashboard{height:100%;position:fixed;background-color:#050514;width:11%}.dashboard #nav-dashboard ul{display:flex;flex-direction:column;width:70%;padding:0;margin:0 auto}.dashboard #nav-dashboard ul li{font-size:1.2vw;text-align:left;width:100%;list-style:none;color:white;margin:0;padding:0.8vw 0}.dashboard #nav-dashboard ul li a{color:white;text-decoration:none;font-family:"Sen",sans-serif;transition:all .1s}.dashboard #nav-dashboard ul li a:hover{color:#d64040}.dashboard #content-dashboard{width:89%;margin-left:11%;height:100%}.dashboard #content-dashboard div.body_dashboard{width:90%;padding-top:0.5vw;margin:0 auto;display:flex;flex-wrap:wrap}.dashboard #content-dashboard div.body_dashboard div.main_dashboard{width:80%;display:flex;flex-wrap:wrap;justify-content:flex-start;align-items:flex-start;align-content:stretch}div.images{width:20vw;height:20vw;display:flex;align-items:center;justify-content:center;margin:2px;position:relative}div.images div{z-index:2;position:absolute;top:0}div.images div img{width:2vw;height:2vw}div.images img{z-index:1;max-width:100%;max-height:100%}div.pdfs{height:20vw;display:flex;flex-direction:column}div.pdfs a{font-size:1.5vw;font-family:"Mukta","sans-serif";text-align:center;width:100%;margin-top:1vw}div.pdfs div{width:15vw;height:15vw;background-size:100%}div.apropos{margin-top:3vw}div.apropos div{width:15vw;height:15vw;margin:0;background-size:100%;background-position:center;background-repeat:no-repeat}div.apropos div.html{width:50%;height:auto}div.apropos div.html p{font-size:1.3vw;font-family:"Mukta","sans-serif";text-align:left;margin:0;width:100%;margin-left:1vw}div.contact,div.category{height:20vw}div.contact p,div.contact a p,div.category p,div.category a p{font-size:1.5vw;font-family:"Mukta","sans-serif";text-align:center}div.contact div,div.category div{width:15vw;height:15vw;background-size:100%;background-position:center;background-repeat:no-repeat}div.rubrique{display